@charset "UTF-8";
body {
  display: none;
}

.header_space {
  display: none;
}

/* mv */
.section_mv {
  width: 100%;
  height: 100svh;
  position: relative;
}

.mv_bg {
  display: block;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.mv_bg.pc {
  display: none;
}

.mv_tree_box {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mv_tree {
  object-fit: cover;
  object-position: center;
  height: 100%;
}

.mv_container {
  width: 100%;
  position: absolute;
  top: 21%;
  left: 50%;
  transform: translateX(-50%);
}

.mv_head {
  font-size: 4vw;
  font-weight: 700;
  line-height: 1.8;
  letter-spacing: 0.01em;
  text-align: center;
}

.mv_logo {
  margin-left: auto;
  margin-right: auto;
  margin-top: 6%;
  width: 50%;
}

/* pickup */
.section_pickup {
  background-color: #EBF677;
  padding-top: 25%;
  padding-bottom: 7%;
  position: relative;
}

.pickup_deco_left,
.pickup_deco_right {
  position: absolute;
}

.pickup_deco_left {
  width: 26%;
  top: 12%;
  left: -9%;
  z-index: 1;
}

.pickup_deco_right {
  width: 28%;
  bottom: -17%;
  right: -11%;
}

.pickup_slide_wrap {
  margin-top: 8%;
}

.pickup_slide_item {
  background-color: #fff;
  border-radius: 4vw;
  padding: 6%;
}

.pickup_slide_thumb {
  border-radius: 4vw;
  overflow: hidden;
}

.pickup_slide_info {
  border-top: 0.7vw solid #F5F5F5;
  margin-top: 6%;
  padding-top: 5%;
}

.pickup_slide_info_inner {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 3.2vw;
}

.pickup_slide_date {
  font-family: "Oswald", sans-serif;
  font-size: 4vw;
  font-weight: 400;
  flex-shrink: 0;
  margin-top: 0.7vw;
}

.pickup_slide_category {
  border: 0.3vw solid #000;
  border-radius: 1.8vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.7vw;
  padding: 1vw 2.6vw;
  min-width: 37vw;
}

.pickup_slide_title {
  display: -webkit-box;
  font-size: 3.7vw;
  line-height: 1.6;
  margin-top: 4%;
  overflow: hidden;
  text-align: justify;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* 行数 */
}

.pickup_slide_wrap .splide__arrows {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.7vw;
  margin-top: 5%;
}

.pickup_slide_wrap .splide__arrows .splide__arrow {
  width: 7.5%;
}

.pickup_slide_wrap .splide__pagination {
  gap: 2.7vw;
}

.pickup_slide_wrap .splide__pagination li {
  aspect-ratio: 71/12;
  width: 9.5vw;
}

.pickup_slide_wrap .splide__pagination .splide__pagination__page {
  background-color: #D9D9D9;
  width: 100%;
  height: 100%;
}

.pickup_slide_wrap .splide__pagination .splide__pagination__page.is-active {
  background-color: #3A7961;
}

/* newsRelease */
.section_newsRelease {
  background-color: #EBF677;
  padding-top: 9%;
  padding-bottom: 28%;
}

.newsRelease_wrap {
  background-color: #fff;
  border-radius: 2.7vw;
  margin-top: 6%;
  margin-left: auto;
  margin-right: auto;
  padding: 8% 5%;
  width: 82%;
}

.newsRelease_inner {
  overflow-y: scroll;
  height: 95vw;
  max-height: 710px;
}

.newsRelease_item {
  display: block;
  padding-top: 8%;
  padding-bottom: 5.5%;
  border-bottom: 0.3vw dotted #212121;
  width: 89%;
}

.newsRelease_item:first-child {
  padding-top: 0;
}

.newsRelease_item_info {
  display: flex;
  align-items: flex-start;
}

.newsRelease_item_date {
  flex-shrink: 0;
  margin-right: 5vw;
  margin-top: 0.8%;
  font-size: 3.7vw;
}

.newsRelease_item_cate_list {
  display: flex;
  flex-wrap: wrap;
  gap: 2.26vw;
  width: 100%;
}

.newsRelease_item_cate {
  border: 0.3vw solid #000;
  border-radius: 1.4vw;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.7vw;
  line-height: 1.5;
  padding: 0.6vw 3.4vw;
  min-width: 28.5vw;
}

.newsRelease_item_title {
  display: -webkit-box;
  font-size: 4.2vw;
  line-height: 1.6;
  margin-top: 4.5%;
  overflow: hidden;
  text-align: justify;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* 行数 */
}

.newsRelease_btn_wrap {
  margin-left: auto;
  margin-right: auto;
  margin-top: 8%;
  width: 80%;
}

/* msg */
.section_msg {
  background-color: #3A7961;
  padding-top: 32%;
  padding-bottom: 38%;
  position: relative;
}

.msg_deco_left,
.msg_deco_right {
  position: absolute;
}

.msg_deco_left {
  width: 25%;
  top: -6.5%;
  left: -3%;
}

.msg_deco_right {
  width: 47%;
  top: -6.5%;
  right: -21%;
}

.msg_container {
  position: relative;
}

.section_msg .page_title_en.-green,
.section_msg .page_title_ja.-green {
  color: #fff;
}

.section_msg .page_title_en.-green::after {
  background-color: #fff;
}

.msg_title {
  color: #fff;
  font-size: 5vw;
  font-weight: 700;
  line-height: 1.8;
  letter-spacing: 0.08em;
  margin-top: 13%;
  text-align: center;
}

.msg_text {
  color: #fff;
  font-size: 4vw;
  font-weight: 700;
  line-height: 2.6;
  letter-spacing: 0.08em;
  margin-top: 6%;
  text-align: center;
}

.msg_btn_wrap {
  margin-left: auto;
  margin-right: auto;
  margin-top: 11%;
  width: 80%;
}

.msg_ingredient {
  cursor: pointer;
  position: absolute;
}

.msg_ingredient.-banana {
  width: 16%;
  top: 35%;
  left: 4%;
}

.msg_ingredient.-avocado {
  width: 11.4%;
  top: 36%;
  right: 9%;
}

.msg_ingredient.-apple {
  width: 12.3%;
  top: 57%;
  left: 6%;
}

.msg_ingredient.-carrot {
  width: 16%;
  top: 51%;
  right: 0%;
}

.msg_ingredient.-pineapple {
  width: 11%;
  top: 72%;
  left: 0%;
}

.msg_ingredient.-pineapple .msg_ingredient_img_box {
  transform: rotate(-45deg);
}

.msg_ingredient.-strawberry {
  width: 9%;
  top: 77%;
  right: 8%;
}

.msg_ingredient::before {
  content: "";
  aspect-ratio: 1/1;
  background-color: #3E9A64;
  background-image: url(../images/common/arrow_right_white.svg);
  background-position: center;
  background-size: 25%;
  background-repeat: no-repeat;
  border: 0.3vw solid #fff;
  border-radius: 50%;
  box-shadow: 0 0.4vw 0.4vw rgba(0, 0, 0, 0.25);
  display: block;
  width: 7.2vw;
  position: absolute;
}

.msg_ingredient.-banana::before {
  top: 0%;
  left: 37%;
}

.msg_ingredient.-avocado::before {
  top: -37%;
  left: 10%;
}

.msg_ingredient.-apple::before {
  top: -33%;
  left: 38%;
}

.msg_ingredient.-carrot::before {
  top: 31%;
  left: -12%;
}

.msg_ingredient.-pineapple::before {
  top: 22%;
  right: -49%;
}

.msg_ingredient.-strawberry::before {
  top: -36%;
  left: -45%;
}

.msg_ingredient_img {
  position: relative;
}

.msg_ingredient_img:hover {
  animation: flameRotate 1.5s infinite;
}

@keyframes flameRotate {
  0% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(-10deg);
  }
  51% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(10deg);
  }
}
.msg_ingredientModal {
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.msg_ingredientModal_inner {
  aspect-ratio: 6/9;
  background-color: #fff;
  border-radius: 4vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow-y: scroll;
  padding: 8% 6% 8%;
  width: 80%;
  max-height: 900px;
  position: relative;
}

.msg_ingredientModal_icon {
  width: 35%;
}

.msg_ingredientModal.-banana .msg_ingredientModal_icon {
  width: 36%;
}

.msg_ingredientModal.-avocado .msg_ingredientModal_icon {
  width: 30%;
}

.msg_ingredientModal.-apple .msg_ingredientModal_icon {
  width: 30%;
}

.msg_ingredientModal.-carrot .msg_ingredientModal_icon {
  width: 26%;
  transform: rotate(19deg);
}

.msg_ingredientModal.-pineapple .msg_ingredientModal_icon {
  width: 20%;
}

.msg_ingredientModal_text {
  color: #3A7961;
  font-size: 4vw;
  font-weight: 700;
  line-height: 1.8;
  margin-top: 11%;
  text-align: center;
}

.msg_ingredientModal_btn {
  margin-top: 17%;
  width: 100%;
}

.msg_ingredientModal_comingSoon {
  color: #3A7961;
  font-size: 6.4vw;
  font-weight: 700;
  letter-spacing: 0.05em;
  margin-top: 10%;
}

.msg_ingredientModal_close {
  cursor: pointer;
  width: 8%;
  position: absolute;
  top: 5%;
  right: 6%;
}

/* ourProducts */
.section_ourProducts {
  background-color: #F5F5F5;
  padding-top: 35%;
  padding-bottom: 63%;
  position: relative;
}

.ourProducts_deco {
  position: absolute;
  top: -6%;
  left: 0;
}

.ourProducts_deco_left {
  display: none;
}

.ourProducts_deco_right {
  display: none;
}

.ourProducts_wrap {
  margin-top: 12%;
  margin-left: auto;
  margin-right: auto;
  width: 82%;
}

.ourProducts_item {
  display: block;
  position: relative;
}

.ourProducts_item:nth-child(n+2) {
  margin-top: 10%;
}

.ourProducts_item_inner {
  background-color: #fff;
  border: 0.6vw solid #D9D9D9;
  border-radius: 4vw;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  overflow: hidden;
}

.ourProducts_item_img {
  width: 47%;
}

.ourProducts_item_info {
  padding: 4% 0% 5% 2%;
  width: 50%;
}

.ourProducts_item_title {
  font-size: 4.8vw;
  font-weight: 700;
}

.ourProducts_item_title::after {
  content: "";
  aspect-ratio: 300/6;
  background-color: #3A7961;
  display: block;
  margin-top: 12%;
  margin-bottom: 8%;
  width: 100%;
}

.ourProducts_item_btn {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 4.2vw;
  margin-right: 12%;
}

.ourProducts_item_btn::after {
  content: "";
  aspect-ratio: 15/17;
  background-image: url(../images/common/arrow_right_green.svg);
  background-size: cover;
  display: block;
  flex-shrink: 0;
  margin-left: 11%;
  margin-top: 2%;
  width: 2.7vw;
}

.ourProducts_item_teamFIcon {
  width: 23%;
  position: absolute;
  top: -21%;
  right: -2%;
}

.ourProducts_btn_wrap {
  margin-top: 14%;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

/* brand */
.section_brand {
  padding-top: 20%;
  padding-bottom: 36%;
  position: relative;
}

.brand_deco_left,
.brand_deco_right {
  position: absolute;
}

.brand_deco_left {
  width: 70%;
  top: -50.5vw;
  left: -22%;
}

.brand_deco_right {
  width: 38%;
  top: 47vw;
  right: -17%;
  z-index: 1;
}

.ourProducts_treeDeco_right {
  width: 62%;
  position: absolute;
  bottom: -3.3%;
  right: 0;
}

.brand_wrap {
  margin-top: 27%;
  margin-left: auto;
  margin-right: auto;
  width: 82%;
}

.brand_inner {
  border: 0.6vw solid #D9D9D9;
  border-radius: 4vw;
  display: block;
  overflow: hidden;
}

.brand_inner:nth-child(n+2) {
  margin-top: 10%;
}

.brand_btn {
  background-color: #F5F5F5;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4.2vw;
  font-weight: 700;
  padding: 5% 13%;
  position: relative;
}

.brand_btn::after {
  content: "";
  aspect-ratio: 15/17;
  background-image: url(../images/common/arrow_right_green.svg);
  background-size: cover;
  display: block;
  width: 2.7vw;
  position: absolute;
  top: 50%;
  right: 10%;
  transform: translateY(-50%);
}

.image_notice {
  display: block;
  font-size: 3.7vw;
  margin-bottom: 32%;
  text-align: center;
}

/* floating_banner */
.floating_banner {
  width: 34%;
  position: absolute;
  bottom: 10%;
  right: 5%;
  z-index: 10;
}

/* floatingMenu */
.floatingMenu_container {
  background-color: #14A73B;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 3.2%;
  padding-bottom: 3.2%;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
  transition: all 0.3s ease-out;
}

.floatingMenu_container.scroll {
  transform: translateY(100%);
}

.floatingMenu__link {
  border-right: 0.3vw solid #fff;
  color: #fff;
  display: block;
  font-size: 3.7vw;
  font-weight: 500;
  letter-spacing: 0.04em;
  padding: 0.5% 2.4vw;
  text-align: center;
}

.floatingMenu__link:first-child {
  border-left: 0.3vw solid #fff;
}

