    width: 17%;
  }
  .msg_ingredientModal.-apple .msg_ingredientModal_icon {
    width: 16%;
  }
  .msg_ingredientModal.-carrot .msg_ingredientModal_icon {
    width: 17%;
  }
  .msg_ingredientModal.-pineapple .msg_ingredientModal_icon {
    width: 13%;
  }
  .msg_ingredientModal_text {
    font-size: min(1.4vw, 30px);
    margin-top: 5%;
  }
  .msg_ingredientModal_btn {
    margin-top: 8%;
    width: 60%;
  }
  .msg_ingredientModal_comingSoon {
    font-size: min(2.2vw, 48px);
    margin-top: 6%;
  }
  .msg_ingredientModal_close {
    width: 4%;
    top: 4%;
    right: 3%;
  }
  /* ourProducts */
  .section_ourProducts {
    padding-top: min(6%, 137px);
    padding-bottom: min(7.5%, 165px);
  }
  .ourProducts_deco {
    display: none;
  }
  .ourProducts_deco_left {
    display: block;
    width: min(18%, 400px);
    position: absolute;
    top: max(-19%, -275px);
    left: 0;
  }
  .ourProducts_deco_right {
    display: block;
    width: min(20%, 440px);
    position: absolute;
    top: max(-19%, -275px);
    right: 0;
  }
  .ourProducts_wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 6%;
    width: 64%;
  }
  .ourProducts_wrap::after {
    content: "";
    display: block;
    width: 32%;
  }
  .ourProducts_item {
    width: 32%;
  }
  .ourProducts_item:nth-child(n+2) {
    margin-top: 0;
  }
  .ourProducts_item:nth-child(n+4) {
    margin-top: 2.3%;
  }
  .ourProducts_item_inner {
    border: min(0.2vw, 4px) solid #D9D9D9;
    border-radius: min(1.1vw, 24px);
  }
  .ourProducts_item_info {
    padding: 4% 0% 5% 2%;
    width: 50%;
  }
  .ourProducts_item_title {
    font-size: min(1.3vw, 28px);
    transition: all 0.3s ease-out;
  }
  .ourProducts_item:hover .ourProducts_item_title {
    color: #3A7961;
  }
  .ourProducts_item_btn {
    font-size: min(1.3vw, 28px);
    transition: all 0.3s ease-out;
  }
  .ourProducts_item_btn::after {
    margin-left: 7%;
    width: min(0.7vw, 15px);
  }
  .ourProducts_item:hover .ourProducts_item_btn {
    color: #3A7961;
  }
  .ourProducts_item_teamFIcon {
    width: 17%;
    top: -8%;
  }
  .ourProducts_btn_wrap {
    margin-top: 8%;
    width: 27.5%;
  }
  /* brand */
  .section_brand {
    padding-top: min(6%, 137px);
    padding-bottom: min(11.5%, 253px);
  }
  .brand_deco_left {
    width: min(19%, 418px);
    top: max(-10.5vw, -230px);
    left: max(-6%, -132px);
  }
  .brand_deco_right {
    width: min(11%, 242px);
    top: max(-1.8vw, -39px);
    right: max(-2.5%, -55px);
  }
  .brand_wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 6%;
    width: 64%;
  }
  .brand_wrap::after {
    content: "";
    display: block;
    width: 31%;
  }
  .brand_inner {
    border: min(0.1vw, 3px) solid #D9D9D9;
    border-radius: min(0.8vw, 18px);
    width: 31%;
  }
  .brand_inner:nth-child(n+2) {
    margin-top: 0%;
  }
  .brand_inner:nth-child(n+4) {
    margin-top: 3%;
  }
  .brand_inner img {
    transition: all 0.3s ease-out;
  }
  .brand_inner:hover img {
    transform: scale(1.1);
  }
  .brand_btn {
    font-size: min(1.3vw, 28px);
    transition: all 0.3s ease-out;
  }
  .brand_btn::after {
    width: min(0.7vw, 15px);
  }
  .brand_inner:hover .brand_btn {
    color: #3A7961;
  }
  .image_notice {
    font-size: min(1.3vw, 28px);
    margin-bottom: 4%;
  }
  /* floating_banner */
  .floating_banner {
    width: 18%;
    bottom: 21%;
    right: 6%;
    transition: all 0.3s ease-out;
  }
  .floating_banner:hover {
    opacity: 0.7;
  }
  /* floatingMenu */
  .floatingMenu_container {
    display: none;
  }
}
@media screen and (min-width: 768px) and (min-aspect-ratio: 220/115) {
  /* mv */
  .mv_container {
    top: 9%;
  }
}